/* Dropdown Menu */.dropdown-menu {
  background-color: #242424;
}

.container-logo-exit {
  display: flex;
  justify-content: flex-end;
}

.menu-exit {
  width: 35px;
  padding: 10px;
}

.dropdown-menu-list {
  text-align: center;
  padding: 220px 0px 220px 0px;
}

.dropdown-menu-link {
  font-size: 5rem;
  font-weight: bold;
  margin: 125px 0px 30px 0px;
  color: #efefef;
  text-decoration: none;
}

.dropdown-menu-link:hover {
  color: #2CEAA3;
  font-size: 5.4rem;
}/* footer */.wrapper {
  min-height: 58vh;
  display: flex;
  flex-direction: column;
}

.page-body {
  flex-grow: 1;
}

.footer {
  flex-shrink: 0;
  height: 130px;
  border-top: 1px solid grey;
  flex-shrink: 0;
  margin: 0px 0px 0px 0px;
  padding: 30px 0px 30px 20px;
  transform: translate(0%, 30%);
  opacity: 0;
  transition: opacity .5s .25s ease-out;
  background-image: linear-gradient(-90deg, #2B2D42, #565977);
}

.footer.is-visible {
  opacity: 1;
}

.social-icon {
  width: 24px;
  margin: 20px 0px 0px 5px;
  padding: 5px 0px 5px 5px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
}

.social-icon.is-visible {
  transform: translate(0px, -25px);
  transition-delay: 300ms;
}

.container-footer {
  width: 100%;
  display: flex;
  flex-direction: row;
  padding: 30px 0px 0px 0px;
  justify-content: flex-end;
  margin-left: -25px;
}

.social-link {
  text-decoration: none;
  padding-bottom: 10px;
  font-size: 1.2rem;
  padding-right: 15px;
}

.social-link:hover {
  text-decoration: line-through;
}

.title-footer {
  font-size: 1.35rem;
  padding-bottom: 5%;
  color: whitesmoke;
}

.subtitle-footer {
  font-size: 1.15rem;
  padding-bottom: 15%;
  color: #354f52;
  color: whitesmoke;
}

.mail-footer {
  font-size: 1.15rem;
  text-decoration: none;
  padding-bottom: 10px;
  font-weight: 700;
  color: whitesmoke;
}

.footer-credits-text {
  flex-shrink: 0;
  color: whitesmoke;
  padding: 10px 0px 0px 0px;
}/* Media Queries for IPAD *//* Media Queries for Desktop *//* Main */.project-container {
  height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 70px 30px 0px 30px;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.project-container.is-visible {
  opacity: 1;
}/* Title Project */.project-title {
  font-size: 1.6rem;
  color: #c53a13;
  letter-spacing: 1px;
  font-weight: 800;
  padding-bottom: 5px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
  text-transform: capitalize;
}

.project-title.is-visible {
  transform: translate(0px, -10px);
}

.project-subtitle {
  font-size: 2.6rem;
  padding-bottom: 20px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
  text-transform: capitalize;
}

.project-subtitle.is-visible {
  transform: translate(0px, -10px);
}

.back-project-container {
  display: flex;
  align-items: center;
  margin-top: -23px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
  z-index: 10;
}

.back-project.is-visible {
  transform: translate(0px, -10px);
}

.arrow-img {
  width: 15px;
  padding-right: 5px;
}/* Horizontal image */.cover-image {
  width: 100vw;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.cover-image.is-visible {
  transform: translate(0px, -10px);
}/* Page section left */.project-page-section {
  position: relative;
  margin-top: 20px;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.project-page-section.is-visible {
  opacity: 1;
}

.project-img-cover {
  width: 100vw;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-img-cover.is-visible {
  transform: translate(0px, 45px);
  padding-bottom: 0px;
}

.text-container-section {
  position: absolute;
  width: 50vw;
  top: -3%;
  left: 50%;
}

.text-section-rigth {
  font-size: 0.9rem;
  font-weight: 500;
  padding: 15px 30px 15px 15px;
}

.text-section-full {
  font-size: 0.9rem;
  font-weight: 500;
  text-align: center;
  padding: 15px 80px 15px 80px;
}/* Technologies section */.technologies-container {
  padding: 0px 30px 0px 30px;
  text-align: center;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.technologies-container.is-visible {
  opacity: 1;
}

.title-technologies {
  font-size: 2rem;
  padding-bottom: 5px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.title-technologies.is-visible {
  transform: translate(0px, -10px);
}

.logos-technologies-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  padding: 10px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 300ms;
}

.logos-technologies-container.is-visible {
  transform: translate(0px, -10px);
}

.technologies-icon {
  width: 25px;
  padding: 5px;
}/* Section Right */.project-page-section-right {
  position: relative;
  margin-top: 20px;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
  text-align: right;
}

.project-page-section-right.is-visible {
  opacity: 1;
}

.project-page-section-right {
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.project-page-section-right.is-visible {
  opacity: 1;
}

.project-img {
  width: 100vw;
  margin-top: -15px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-img-full {
  width: 100vw;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-img-full.is-visible {
  transform: translate(0px, -10px);
}

.project-img.is-visible {
  transform: translate(0px, -10px);
}

.text-container-section-two {
  position: absolute;
  width: 50vw;
  top: 30%;
}

.text-section {
  font-size: 0.9rem;
  text-align: left;
  font-weight: 500;
  padding: 0px 80px 15px 25px;
}/* Buttons section */.buttons-container {
  display: flex;
  flex-direction: column;
  margin: 10px 0px 50px 0px;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.buttons-container.is-visible {
  opacity: 1;
}

.project-button-left {
  font-size: 1.8rem;
  text-decoration: none;
  padding: 5px;
  color: #242424;
  font-weight: 700;
  margin: 0px 0px 0px 70px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-button-left:hover {
  text-decoration: line-through;
}

.project-button-left.is-visible {
  transform: translate(0px, -10px);
}

.project-button-right {
  font-size: 1.4rem;
  text-decoration: none;
  padding: 5px;
  color: #242424;
  font-weight: 700;
  margin: 0px 0px 20px 200px;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.project-button-right:hover {
  text-decoration: line-through;
}

.project-button-right.is-visible {
  transform: translate(0px, -10px);
}

.project-button:hover {
  font-size: 2.3rem;
}/* Media Queries for IPAD *//* Media Queries for Desktop */* {
  margin: 0px;
  padding: 0px;
  font-size: 10px;
  font-family: 'Montserrat', sans-serif;
  color: #242424;
  font-weight: 300;
}

body {
  background-color: #f8f8f8;
}/* KeyFrames */.about-me-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity .5s .25s ease-out;
}

.about-me-container.is-visible {
  opacity: 1;
}

.image-profile-container {
  position: relative;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
}

.image-profile-container.is-visible {
  transform: translate(-24%, -1%);
}

.image-profile {
  width: 94vw;
}

.text-container {
  position: absolute;
  padding: 0% 10% 0% 10%;
  z-index: 1;
  transition: transform 10s 0.25s cubic-bezier(0, 1, 0.3, 1);
  transition-delay: 200ms;
  width: 100vw;
  height: 100vh;
}

.text-container.is-visible {
  transform: translate(0%, -5%);
}

.title-profile {
  font-size: 4rem;
  font-weight: 900;
  transform: translate(69%, -417%);
  min-width: 100vw;
}

.text-profile-first-paragraphs {
  margin: -5% 5% 6%;
  font-size: 1.1rem;
  transform: translate(5%, 205%);
  width: 83%;
}

.text-profile-last-paragraphs {
  margin: 5% 5% 6%;
  font-size: 1.1rem;
  transform: translate(5%, 114%);
  width: 83%;
}

.links-profile-container {
  display: flex;
  flex-direction: column;
  transform: translate(-31%, 980%);
  min-width: 100vw !important;
}

.link-profile {
  font-weight: 700;
  font-size: 1.3rem;
  text-decoration: none;
  padding-bottom: 3px;
}

.link-profile:hover {
  text-decoration: line-through;
