  /* KeyFrames */
  
  @keyframes opacity {
    0% {
      transform: translate(100px, -250px);
      opacity: 0;
    }
    50%{
      transform: translate(0px, 0px);
      opacity: 0.5;
    }
    100% {
      transition: transform 0.2s;
      opacity: 1;
    }
  }


.about-me-container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: opacity .5s .25s ease-out;
}

.about-me-container.is-visible {
    opacity: 1;
}

