.image-profile-container {
    position: relative;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
    transition-delay: 200ms;
}

.image-profile-container.is-visible {
    transform: translate(-24%, -1%);
}

.image-profile {
    width: 94vw;
}

.text-container {
    position: absolute;
    padding: 0% 10% 0% 10%;
    z-index: 1;
    transition: transform 10s .25s cubic-bezier(0,1,.3,1);
    transition-delay: 200ms;
    width: 100vw;
    height: 100vh;
}

.text-container.is-visible {
    transform: translate(0%, -5%);
}

.title-profile {
    font-size: 4rem;
    font-weight: 900;
    transform: translate(69%, -417%);
    min-width: 100vw;
}

.text-profile-first-paragraphs {
    margin: -5% 5% 6%;
    font-size: 1.1rem;
    transform: translate(5%, 205%);
    width: 83%;
}

.text-profile-last-paragraphs {
    margin: 5% 5% 6%;
    font-size: 1.1rem;
    transform: translate(5%, 114%);
    width: 83%;
}

.links-profile-container {
    display: flex;
    flex-direction: column;
    transform: translate(-31%, 980%);
    min-width: 100vw !important;
}

.link-profile {
    font-weight: 700;
    font-size: 1.3rem;
    text-decoration: none;
    padding-bottom: 3px;
}

.link-profile:hover {
    text-decoration: line-through;
}



